@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.6em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #333333;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}
.container.margin-top {
  margin-top: 180px;
}

h1 {
  font-weight: 500;
  font-size: 36px;
  margin-bottom: 18px;
  font-family: "Roboto", sans-serif;
}

h2 {
  font-weight: 500;
  font-size: 26px;
  margin-bottom: 18px;
  font-family: "Roboto", sans-serif;
}

p {
  font-size: 14px;
  margin-bottom: 10px;
}

.inner-space {
  padding: 40px 80px;
}

.outer-space {
  margin: 40px 0;
}

header {
  height: 120px;
  display: flex;
  align-items: center;
  background-color: #EFF2F5;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 5;
}
header .container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
header .container .logo {
  display: flex;
  align-items: center;
  margin: 0 40px;
}
header .container .logo img {
  height: 90px;
}
header .container nav {
  margin: 0 40px;
  display: flex;
  align-items: center;
}
header .container nav ul {
  list-style-type: none;
  display: flex;
}
header .container nav ul li {
  margin: 0 5px;
}
header .container nav ul li a {
  font-size: 18px;
  font-weight: 500;
  padding: 0 10px;
}
header .container nav ul li a:hover {
  color: #1F918b;
}

.hero {
  height: 400px;
  scroll-margin-top: 180px;
  position: relative;
  overflow: hidden;
}
.hero .slides {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  transition: left 1s ease-out;
}
.hero .slides .slide {
  display: flex;
  width: 100%;
  height: 100%;
  flex-shrink: 0;
}
.hero .slides .slide img {
  object-fit: cover;
  object-position: center;
  width: 100%;
}
.hero .bars {
  display: flex;
  justify-content: center;
  position: absolute;
  top: 93%;
  left: 50%;
  transform: translateX(-50%);
}
.hero .bars .bar-wrapper {
  padding: 10px;
  cursor: pointer;
}
.hero .bars .bar-wrapper .bar {
  height: 3px;
  background-color: #fff;
  width: 70px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}
.hero .bars .bar-wrapper .bar.active {
  background-color: #34D1C9;
}

.about {
  min-height: 300px;
  background-color: #1F918b;
  color: #fff;
  scroll-margin-top: 160px;
}
.about h1 {
  color: #fff;
}
.about p {
  color: #fff;
}

.offer {
  scroll-margin-top: 160px;
}
.offer .offer-container {
  margin: 40px 0;
  display: flex;
  gap: 40px;
}
.offer .offer-container .picture {
  width: 50%;
  overflow: hidden;
  display: flex;
}
.offer .offer-container .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
}
.offer .offer-container .text {
  width: 50%;
  padding: 15px 20px;
}
.offer .offer-container:nth-of-type(2n) {
  flex-direction: row-reverse;
}
.offer .offer-container:nth-of-type(1) {
  margin-top: 20px;
}

.contact {
  min-height: 800px;
  scroll-margin-top: 160px;
}
.contact .contact-container {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
}
.contact .contact-container .form {
  width: calc(50% - 30px);
  min-height: 300px;
  position: relative;
}
.contact .contact-container .form form {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  opacity: 1;
  transition: opacity 0.5s;
}
.contact .contact-container .form form input,
.contact .contact-container .form form textarea {
  border: 1px solid #82868A;
  padding: 8px 12px;
  font-size: 13px;
  width: 100%;
  font-weight: 500;
}
.contact .contact-container .form form input.active,
.contact .contact-container .form form textarea.active {
  border-color: #34D1C9;
}
.contact .contact-container .form form .input-container50 {
  width: calc(50% - 10px);
  position: relative;
}
.contact .contact-container .form form .input-container100 {
  width: 100%;
  position: relative;
}
.contact .contact-container .form form textarea[name=message] {
  width: 100%;
  height: 100px;
  line-height: 1.2em;
}
.contact .contact-container .form form .button-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 5px;
  align-items: center;
}
.contact .contact-container .form form .button-container .sending-info {
  display: flex;
  margin-bottom: 0;
  font-size: 14px;
}
.contact .contact-container .form form .button-container .sending-info.success-info {
  color: #1F918b;
}
.contact .contact-container .form form .button-container .sending-info.error-info {
  color: #D33212;
}
.contact .contact-container .form form .button-container button {
  padding: 10px 40px;
  font-size: 14px;
  background-color: #1F918b;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  transition: all 0.1s;
}
.contact .contact-container .form form .button-container button:hover {
  background-color: #fff;
  color: #1F918b;
  border: 1px solid #1F918b;
}
.contact .contact-container .form form .button-container button.loading {
  position: relative;
  pointer-events: none;
  opacity: 0.5;
}
.contact .contact-container .form form .button-container button.loading::after {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-right-color: rgba(0, 0, 0, 0.7);
  transform: translate(-50%, -50%) rotate(0deg);
  content: "";
  animation: rotateSingleLoading 0.6s infinite linear;
  z-index: 10;
}
@keyframes rotateSingleLoading {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.contact .contact-container .form form p.error {
  margin: 0;
  margin-top: -2px;
  margin-left: 5px;
  font-size: 12px;
  color: #D33212;
  position: absolute;
}
.contact .contact-container .form form input.error,
.contact .contact-container .form form textarea.error {
  border-color: #D33212;
}
.contact .contact-container .form .successSendingMessage {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  transition: opacity 0.5s;
  visibility: hidden;
  width: 100%;
}
.contact .contact-container .form .successSendingMessage h2 {
  margin-bottom: 0;
  color: #1F918b;
}
.contact .contact-container .form .successSendingMessage p {
  text-align: center;
}
.contact .contact-container .address-data {
  width: calc(50% - 30px);
  min-height: 300px;
  background-color: #1F918b;
  padding: 30px 40px;
}
.contact .contact-container .address-data h2 {
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 30px;
}
.contact .contact-container .address-data .address-line {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.contact .contact-container .address-data .address-line a,
.contact .contact-container .address-data .address-line p {
  color: #fff;
  display: block;
  font-size: 16px;
  margin-bottom: 0;
  transition: all 0.2s;
}
.contact .contact-container .address-data .address-line a:hover {
  letter-spacing: 0.5px;
}
.contact .contact-container .address-data .address-line .img-container {
  display: flex;
  justify-content: center;
  width: 40px;
}
.contact .contact-container .address-data .address-line .img-container img {
  height: 20px;
}
.contact .contact-container .map {
  width: 100%;
  min-height: 600px;
}
.contact .contact-container .map iframe {
  width: 100%;
}

footer {
  min-height: 100px;
  background-color: #25272A;
}
footer .top-footer .container {
  display: flex;
  padding: 40px 100px;
  justify-content: space-between;
}
footer .top-footer .container .top-footer-box {
  width: 230px;
  display: flex;
  justify-content: center;
}
footer .top-footer .container .logo-socials {
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer .top-footer .container .logo-socials img {
  height: 80px;
  justify-content: left;
}
footer .top-footer .container .logo-socials .socials {
  margin-top: 15px;
  display: flex;
  gap: 16px;
}
footer .top-footer .container .logo-socials .socials img {
  height: 17px;
  cursor: pointer;
  transition: scale 0.1s ease-out;
}
footer .top-footer .container .logo-socials .socials img:hover {
  scale: 1.5;
}
footer .top-footer .container .contact-footer h3 {
  color: #82868A;
  font-weight: 700;
  margin-bottom: 16px;
  font-size: 22px;
}
footer .top-footer .container .contact-footer a,
footer .top-footer .container .contact-footer p {
  color: #82868A;
  display: block;
  font-size: 14px;
  margin-bottom: 0;
  font-weight: 300;
}
footer .top-footer .container .contact-footer a:hover {
  color: #1F918b;
}
footer .top-footer .container .menu-footer h3 {
  color: #82868A;
  font-weight: 700;
  margin-bottom: 16px;
  font-size: 22px;
}
footer .top-footer .container .menu-footer ul {
  list-style-type: none;
}
footer .top-footer .container .menu-footer a,
footer .top-footer .container .menu-footer p {
  color: #82868A;
  display: block;
  font-size: 14px;
  margin-bottom: 0;
  font-weight: 300;
}
footer .top-footer .container .menu-footer a:hover {
  color: #1F918b;
}
footer .bottom-footer {
  border-top: 1px solid #313234;
  min-height: 60px;
  display: flex;
  align-items: center;
}
footer .bottom-footer .container {
  display: flex;
  padding: 0 100px;
  justify-content: space-between;
  width: 100%;
}
footer .bottom-footer .container .copyrights {
  color: #82868A;
  font-size: 14px;
}
footer .bottom-footer .container .regulations {
  font-size: 14px;
  color: #82868A;
}
footer .bottom-footer .container .regulations a {
  color: #82868A;
}
footer .bottom-footer .container .regulations a:hover {
  color: #1F918b;
}

@media (max-width: 1440px) {
  .container {
    max-width: 1024px;
  }
  .outer-space {
    margin: 0;
  }
  .about,
  .offer,
  .contact {
    scroll-margin-top: 120px;
  }
}
@media (max-width: 1024px) {
  .container {
    max-width: 100%;
  }
  .container.margin-top {
    margin-top: 120px;
  }
  header {
    height: 80px;
  }
  header .container .logo img {
    height: 50px;
  }
  header .container nav ul li a {
    font-size: 14px;
  }
  .outer-space {
    margin: 50px 0;
  }
  .about,
  .offer,
  .contact {
    scroll-margin-top: 80px;
  }
  .outer-space.about {
    margin-bottom: 0;
  }
  .outer-space.offer {
    margin-top: 0;
    margin-bottom: 0;
  }
  .outer-space.contact {
    margin-top: 0;
    margin-bottom: 0;
  }
  .offer {
    padding-bottom: 0;
  }
  .offer-container:last-of-type {
    margin-bottom: 0;
  }
  .contact .contact-container .address-data {
    padding: 30px;
  }
  .contact .contact-container .address-data .address-line a,
  .contact .contact-container .address-data .address-line p {
    font-size: 14px;
  }
  .contact .contact-container .form form .input-container50 {
    width: 100%;
  }
  .contact .contact-container .map {
    width: 150%;
    margin-left: -80px;
    margin-right: -80px;
  }
  footer .top-footer .container {
    padding: 40px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 100%;
  }
  header .container nav ul.show {
    left: 0;
    transition: left ease-out 0.5s;
  }
  header .container nav ul {
    transition: left ease-out 0.5s;
    display: flex;
    position: fixed;
    top: 0;
    left: -500px;
    min-height: 100vh;
    min-width: fit-content;
    width: 30%;
    background: white;
    z-index: 3;
    flex-direction: column;
    padding: 80px 15px 0 15px;
  }
  header .container nav ul li {
    padding: 10px;
  }
  header .container nav ul li a {
    font-size: 20px;
  }
  header .container nav .hamburger {
    padding: 10px 15px;
    border: solid 1px #1F918b;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
  }
  header .container nav .hamburger .bars {
    position: relative;
    cursor: pointer;
  }
  header .container nav .hamburger .bars .icon-bar {
    background-color: #1F918b;
    display: block;
    width: 15px;
    height: 2px;
    position: relative;
    transition: all 0.3s;
  }
  header .container nav .hamburger .bars .icon-bar:nth-child(1) {
    transform: rotate(45deg);
    top: 5px;
  }
  header .container nav .hamburger .bars .icon-bar:nth-child(2) {
    opacity: 0;
    margin: 3px 0;
  }
  header .container nav .hamburger .bars .icon-bar:nth-child(3) {
    transform: rotate(-45deg);
    bottom: 5px;
  }
  header .container nav .hamburger .bars.ham .icon-bar:nth-child(1) {
    transform: rotate(0);
    top: 0;
  }
  header .container nav .hamburger .bars.ham .icon-bar:nth-child(2) {
    opacity: 1;
    margin: 3px 0;
  }
  header .container nav .hamburger .bars.ham .icon-bar:nth-child(3) {
    transform: rotate(0);
    bottom: 0;
  }
  .inner-space {
    padding: 40px 60px;
  }
  .outer-space {
    margin: 0;
  }
  .offer.inner-space {
    padding-bottom: 0;
  }
  .offer .offer-container {
    flex-direction: column-reverse;
  }
  .offer .offer-container .picture {
    width: 100%;
  }
  .offer .offer-container .text {
    width: 100%;
    padding: 0;
  }
  .offer .offer-container:nth-of-type(2n) {
    flex-direction: column-reverse;
  }
  .offer .offer-container:last-of-type {
    margin-bottom: 0;
  }
  .contact .contact-container {
    gap: 40px;
  }
  .contact .contact-container .form {
    width: 100%;
  }
  .contact .contact-container .form form {
    margin: 0;
  }
  .contact .contact-container .address-data {
    width: 100%;
    min-height: 250px;
  }
  .contact .contact-container .map {
    width: 150%;
    margin-left: -60px;
    margin-right: -60px;
  }
  footer .top-footer .container {
    padding: 40px 20px 30px;
    justify-content: center;
  }
  footer .top-footer .container .top-footer-box:nth-of-type(2), footer .top-footer .container .top-footer-box:nth-of-type(3) {
    display: none;
  }
  footer .bottom-footer .container {
    padding: 0 30px;
  }
}
@media (max-width: 480px) {
  .container {
    max-width: 100%;
  }
  .inner-space {
    padding: 40px;
  }
  .contact .contact-container {
    gap: 40px;
  }
  .contact .contact-container .address-data {
    margin-left: -40px;
    margin-right: -40px;
    width: 140%;
  }
  .contact .contact-container .form form {
    margin: 0;
  }
  .contact .contact-container .map {
    width: 140%;
    margin-left: -40px;
    margin-right: -40px;
  }
  footer .bottom-footer .container {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding: 0;
    width: 100%;
    padding: 12px 0;
  }
  footer .bottom-footer .container .copyrights {
    margin-top: 6px;
  }
}